import React from 'react';
import styled from 'styled-components';
import { CenteredDiv, Color, yellow, responsiveSizes } from './Styles';
import NavBar from './NavBar';
import Chevron from './Chevron';

const HeroBg = styled.div`
  height: 100vh;
  background-image: url(${props => props.img});
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.7);
  background-blend-mode: darken;
  background-position: bottom;

  color: white;
`;

const HeroText = styled(CenteredDiv)`
  padding-top: 12vh;
  width: 100%;

  @media screen and (max-width: ${responsiveSizes.smallScreen}px) {
    display: none;
  }
`;

const HeroTextMobile = styled(CenteredDiv)`
  display: none;

  @media screen and (max-width: ${responsiveSizes.smallScreen}px) {
    display: block;
    padding-top: 5vh;
  }
`;

const Highlight = styled.div`
  background-color: ${yellow};
  padding: 1rem 2rem;
  color: white;
  width: max-content;
  height: auto;
  margin: auto auto;
  letter-spacing: 0.5rem;
`;

const TagLine = styled.h1`
  margin-bottom: ${props => props.mb || '1rem'};
`;

export const HeroBlock = ({ children }) => (
  <Highlight>
    <Color color="white"><h1>{children}</h1></Color>
  </Highlight>
);

const Hero = () => (
  <HeroBg img="static/robots.png">
      <NavBar />

      {/* screen > 1024 */}
      <HeroText>
        <TagLine>learn <Color color={yellow}> / </Color> build</TagLine>
        <TagLine mb="2rem">program <Color color={yellow}> / </Color> compete</TagLine>
        <HeroBlock>U WAT VEX U ROBOTICS</HeroBlock>
      </HeroText>

      {/* screen <= 1024 */}
      <HeroTextMobile>
        <TagLine>learn <Color color={yellow}> / </Color> build</TagLine>
        <TagLine mb="2rem">program <Color color={yellow}> / </Color> compete</TagLine>
        <HeroBlock>U WAT VEX U ROBOTICS</HeroBlock>
      </HeroTextMobile>

      <Chevron href="#about" />
  </HeroBg>
);

export default Hero;
